#navbar {
	/* background: #1f2235; */
	height: 70px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 22px;
	max-width: 100vw;
	position: relative;
}

.nav-menu {
	display: flex;
	list-style: none;
	text-align: center;
	/* justify-content: start; */
}

#navbar h2 {
	padding-left: 30px;
}

#hello {
	font-size: 24px;
	color: #f7b538;
	font-family: 'Merienda', cursive;
	padding-top: 30px;
	max-width: 900px;
	margin: 50px auto;
}
#hello h1 {
	font-family: 'The Historia Demo';
	font-size: 135px;
	background: linear-gradient(to top, #fa320f, #ffc738);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	font-weight: 500;
}

#hello h2:first-of-type {
	font-family: 'The Historia Demo';
	color: #f7b538;
	font-size: 42px;
	font-weight: 300;
}

#hello h2 {
	color: #f7b538;
	font-size: 26px;
	font-weight: 300;
}

.nav-item {
	display: flex;
	align-items: center;
	height: 70px;
	line-height: 20px;
	padding: 15px;
}

.nav-dev {
	display: flex;
	align-items: center;
	height: 70px;
	line-height: 20px;
	color: #fe4a57;
	text-decoration: none;
	padding: 8px 15px;
	font-family: 'The Historia Demo', sans-serif;
}

.nav-links {
	color: #fe4a57;
	text-decoration: none;
	padding: 8px 14px;
	font-family: 'The Historia Demo', sans-serif;
	font-size: 34px;
}

.social-icons {
	color: #fe4a57;
	font-size: 32px;
	padding-right: 30px;
	display: flex;
}

.social-icons a {
	padding-left: 10px;
}

.social-icons a:visited {
	color: #fe4a57;
}

.nav-links:hover {
	background-color: #f7b538;
	color: #1f2235;
	border-radius: 4px;
	transition: all 0.2s ease-out;
	cursor: pointer;
}

.fa-bars {
	color: #fe4a57;
}
.fa-times:before {
	color: #fe4a57;
}

.menu-icon {
	display: none;
}

@media screen and (max-width: 768px) {
	.nav-menu {
		display: flex;
		flex-direction: column;
		width: 100vw;
		max-width: 100vw;
		height: 100vh;
		position: absolute;
		top: 0px;
		bottom: 0px;
		left: -100vw;
		opacity: 1;
		transition: all 0.5s ease;
	}

	.nav-menu.active {
		background: #1f2235;
		left: 0;
		opacity: 1;
		transition: all 0.5s ease;
		z-index: 1;
	}

	.nav-links {
		text-align: center;
		padding: 16px;
		width: 100vw;
		display: table;
		font-size: 30px;
		margin-left: -30px;
	}

	.nav-links:hover {
		background-color: #f7b538;
		color: #1f2235;
		border-radius: 0;
	}

	#navbar-logo {
		position: absolute;
		top: 0;
		left: 0;
		transform: translate(25%, 50%);
	}

	.menu-icon {
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		transform: translate(-100%, 60%);
		font-size: 29px;
		cursor: pointer;
	}

	.fa-times {
		font-size: 32px;
	}

	.social-icons {
		width: 90px;
		position: absolute;
		left: calc(50% - 45px);
		font-size: 28px;
	}
}

@media screen and (min-width: 1700px) {
}
@media screen and (max-width: 768px) {
	#hello h1 {
		font-size: 120px;
	}
	#hello h2 {
		font-size: 30px;
	}
	#about > h2 {
		font-size: 12px;
	}
}
@media screen and (max-width: 580px) {
	#hello h1 {
		font-size: 100px;
	}
	#hello h2 {
		font-size: 24px;
	}
	#about > h2 {
		font-size: 24px;
	}
}

#about {
	/* min-height: calc(100vh - 120px); */
	max-width: 1600px;
	margin: 0 auto;
	padding-bottom: 25px;
}
/* /////////////////GREETING///////////////////// */

/* /////////////////INTRO//////////////////////// */

.intro {
	display: grid;
	grid-template-columns: 2fr 1fr 2fr;
	position: relative;
	padding-top: 80px;
}
.intro p {
	font-family: 'Merienda', cursive;
	font-size: 24px;
	color: #f7b538;
	width: 80%;
	margin: 35px auto;
}
.intro hr {
	border: 1px solid #fe4a57;
}

.intro div {
	position: relative;
	text-align: justify;
}

#about > h2 {
	font-family: 'Merienda', cursive;
	color: #f7b538;
	font-size: 32px;
	font-weight: 300;
}
/* ///////////////////////////////////////////// */

@media screen and (max-width: 768px) {
	.intro {
		display: flex;
		flex-direction: column;
	}
	.intro :nth-child(1) {
		order: 3;
	}
	.intro :nth-child(3) {
		order: 3;
	}
}

#skills {
	padding: 50px;
	position: relative;
	color: #fe4a57;
	font-family: 'The Historia Demo', sans-serif;
	text-align: center;
}

#skills div {
	line-height: 2;
}

#skills h2 {
	font-size: 42px;
	font-weight: 300;
}

#skills h3 {
	font-size: 26px;
	font-weight: 300;
	font-family: 'Merienda', cursive;
	color: #f7b538;
}

.techstack {
	display: flex;
	flex-flow: row;
	width: 75%;
	max-width: 1080px;
	flex-wrap: wrap;
	justify-content: space-around;
	padding-top: 40px;
	padding: 20px;
	text-align: center;
	margin: 0 auto;
}

.tech-icons {
	margin: 15px auto;
	border-radius: 5px;
}

.techstack p {
	font-family: 'Merienda', cursive;
}

.tech-icons img {
	margin: 10px 20px;
	border-radius: 5px;
	max-height: 95px;
}

@media screen and (max-width: 1024px) {
	.techstack {
		width: 90%;
	}

	.tech-icons {
		margin: 10px 10px;
		border-radius: 5px;
	}
	.tech-icons img {
		margin: 8px 18px;
		max-height: 75px;
	}
}
@media screen and (max-width: 720px) {
	.techstack {
		width: 95%;
	}
	.tech-icons img {
		margin: 5px 12px;
		max-height: 55px;
	}
}

#projects {
	margin-bottom: 120px;
	padding-top: 0px;
	min-height: 50vh;
	display: grid;
}

#projects h2 {
	color: #fe4a57;
	font-family: 'The Historia Demo', sans-serif;
	font-weight: 300;
	font-size: 62px;
}

.projects-div {
	margin: 20px auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	max-width: 1400px;
}

.project-card {
	height: 200px;
	width: 380px;
	border-radius: 5px;
	margin-bottom: 20px;
	position: relative;
}

.btn {
	height: 30px;
	width: 80px;
	border: 2px solid #fe4a57;
	border-radius: 10px;
	opacity: 1;
	font-size: 16px;
	font-family: 'Merienda', cursive;
}

.card {
	position: relative;
	margin-top: 50px;
	width: 500px;
	height: 300px;
	border-radius: 20px;
	font-family: 'The Historia Demo', sans-serif;
	overflow: hidden;
}

.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0);
	transition: all 0.5s ease;
}

.card:hover .overlay {
	display: block;
	background: rgba(0, 0, 0, 0.8);
}

.card img {
	position: absolute;
	width: 500px;
	height: 300px;
	left: 0;
}

.title,
.proj-description {
	position: absolute;
	width: 500px;
	top: 220px;
	font-weight: 300;
	text-align: center;
	transition: top 0.5s ease;
	z-index: 1;
	opacity: 0;
}

.title {
	font-size: 32px;
	color: #fe4a57;
}

.proj-description {
	font-family: 'Merienda', cursive;
	font-size: 18px;
	text-align: center;
	padding: 0 30px;
	line-height: 1.5;
	color: #f7b538;
}

.card:hover p {
	opacity: 1;
}

.card:hover .proj-description {
	top: 65px;
	color: #f7b538;
}
.card:hover .title {
	top: 35px;
}

.button-wrap {
	position: absolute;
	width: 500px;
	left: 0;
	bottom: 20px;
	text-align: center;
	opacity: 0;
	transition: opacity 0.35s ease;
}
.card:hover .button-wrap {
	opacity: 1;
}
.btn {
	background-color: #1f2235;
	color: #fe4a57;
}
.btn:hover {
	cursor: pointer;
	background-color: #f7b538;
	color: #1f2235;
	border: 2px solid #1f2235;
}

.button-wrap a {
	width: 100px;
	padding: 5px;
	text-align: center;
	z-index: 1;
}
@media screen and (max-width: 1024px) {
	#projects h2 {
		font-size: 56px;
		width: 75%;
		margin: 0 auto;
	}
}

@media screen and (max-width: 768px) {
	#projects h2 {
		font-size: 48px;
		width: 65%;
	}
	.card {
		position: relative;
		margin-top: 50px;
		width: 420px;
		height: 280px;
		border-radius: 20px;
		font-family: 'The Historia Demo', sans-serif;
		overflow: hidden;
	}
	.card img {
		width: 420px;
		height: 280px;
	}
	.overlay {
		display: block;
		background: rgba(0, 0, 0, 0.65);
	}
	.title {
		width: 420px;
		top: 20px;
		left: 0;
		font-weight: 300;
		font-size: 35px;
		z-index: 1;
		opacity: 1;
		margin: 0 auto;
	}
	.proj-description {
		font-size: 16px;
		padding: 0;
		opacity: 1;
		top: 60px;
		line-height: 1.5;
		width: 100%;
		margin: 0 auto;
	}
	.button-wrap {
		opacity: 1;
		width: 420px;
	}
	.card:hover .title {
		top: 20px;
	}
	.btn {
		background-color: #f7b538;
		color: #1f2235;
	}
}

@media screen and (max-width: 540px) {
	#projects h2 {
		font-size: 40px;
		width: 65%;
	}
	.card {
		margin-top: 50px;
		width: 360px;
		height: 240px;
		border-radius: 20px;
		overflow: hidden;
	}
	.card img {
		width: 360px;
		height: 240px;
	}
	.title {
		width: 360px;
		top: 20px;
		left: 0;
		font-size: 26px;
		z-index: 1;
		opacity: 1;
		margin: 0 auto;
	}
	.button-wrap {
		opacity: 1;
		width: 360px;
	}
	.proj-description {
		font-size: 15px;
		margin: 0 auto;
	}
}

#contact {
	display: grid;
	display: flex;
	flex-direction: column;
	width: 40vw;
	max-width: 800px;
	min-width: 360px;
	margin: 0 auto;
	margin-bottom: 100px;
}

#contact {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

#contact > h2 {
	margin-bottom: 30px;
	color: #fe4a57;
	font-family: 'The Historia Demo', sans-serif;
	font-weight: 300;
	font-size: 40px;
}

#contact > input,
textarea {
	padding: 10px;
	border-radius: 3px;
	margin-bottom: 20px;
	/* border: none; */
	background: #fff;
	font-size: 16px;
	color: rgb(0, 0, 32);
	outline: none;
	border: 3px solid #fe4a57;
}

#contact > input:focus,
textarea:focus {
	border: 3px solid #f7b538;
}

#contact > textarea {
	height: 150px;
	max-width: 100%;
	min-height: 100px;
}

#contact > label {
	padding-bottom: 5px;
	color: #f7b538;
	font-weight: bold;
	font-family: 'Merienda', cursive;
	font-size: 18px;
}

#contact > input:last-of-type {
	color: #fe4a57;
	background: #1f2235;
	text-decoration: none;
	padding: 8px 14px;
	font-family: 'The Historia Demo', sans-serif;
	font-size: 26px;
	width: 150px;
	border-radius: 10px;
	margin: 0 auto;
}

#contact > input:last-of-type:hover {
	background-color: #f7b538;
	color: #1f2235;
	transition: all 0.3s ease-out;
	cursor: pointer;
	border: 3px solid #1f2235;
}
::-webkit-input-placeholder {
	color: #fe4a57;
	font-family: 'Merienda', cursive;
	font-size: 12px;
}
.contact-info {
	margin: 20px auto;
	width: 100%;
	font-size: 28px;
	color: #f7b538;
}
.contact-info > * {
	padding: 10px;
}
.contact-info a,
contact-info a:visited,
contact-info a:link {
	color: #f7b538;
	text-decoration: none;
}

.home-top {
	height: 40px;
	color: #f7b538;
	text-decoration: none;
	padding: 8px 15px;
	font-family: 'Merienda', cursive;
	width: 25%;
	font-size: 18px;
	min-width: 180px;
	margin: 0 auto;
	border: 1px #f7b538 solid;
	border-radius: 8px;
}

#footer {
	/* position: fixed; */
	bottom: 0;
	max-width: 100vw;
	width: 100%;
	background-color: #1f2235;
	height: 50px;
	align-content: center;
	font-family: 'Merienda', cursive;
	z-index: 1;
	overflow: hidden;
}

#footer p {
	font-size: 17px;
	text-align: center;
	color: #fe4a57;
}

